import { Color } from "../generated/api"
import { Cell } from "../models"

export const initCells = (): Cell[][] => {
  let cells = []
  for (let y = 0; y < 8; y++) {
    const row: Cell[] = []
    for (let x = 0; x < 8; x++) {
      row.push({
        position: { x, y },
        figure: null,
        color: (y + x) % 2 ? Color.BLACK : Color.WHITE,
        available: false,
        moveId: "",
      })
    }
    cells.push(row)
  }
  return cells
}
